li{
    list-style-type: none;
}
a{
    text-decoration: none;
}
.img-contact{
    width: 50px;
    border: 3px solid #ffbe00;;
    border-radius: 34%;
}
.contact-prev{
    background-color: #003b67;
    border-radius: 26px;
    display: flex;
    justify-content: space-between;
    align-items: center;
    margin: 10px;
    cursor: pointer;
    color: white;
}
.contact-prev:hover{
    background-color: #1c5785;
}
.contact-prev span{
    margin-left: 25px;
}

.statistic-container{
    background-color: #003b67;
    margin: 30px;
    border-radius: 30px;
}
.statistic-container h4{
    display: flex;
    justify-content: center;
    color: white;
    font-size: 20px;
    text-decoration: underline;
}
.search{
    height: 50px;
    width: 300px;
    padding: 0 10px;
    border: 0;
    border-radius: 13px;
    color: #003b67;
    outline: 0;
    font-weight: bold;
    font-size: 20px;
    transition: all .3s;
    margin-top: 9px;
}
.search:focus{
    width: 350px;
}
.btn{
    position: fixed;
    bottom: 20px;
    left: 220px;
    border: 0;
    padding: 10px;
    background-color: white;
    cursor: pointer;
    color: #003b67;
    font-weight: bold;
    transition: all .3s;
    border-radius: 10px;
}
.btn:hover{
    background-color: #efefef;
}

.signup-input{
    height: 50px;
    border: 0;
    border-radius: 10px;
    padding-left: 10px;
    margin-top: 15px;
    font-weight: bold;
    outline: 0;
}
.signup-btn{
    border: 0;
    padding: 10px;
    background-color: white;
    cursor: pointer;
    color: #003b67;
    font-weight: bold;
    transition: all .3s;
    margin: 10px;
    border-radius: 10px;
}
.signup-btn:hover{
    background-color: #efefef;
}
.details-container{
    display: flex;
    flex-direction: column;
    align-items: center;
    background-color: #003b67;
    color: white;
    margin: 20px;
    border-radius: 15px;
    padding: 20px;
}

.edit-contact{
    background-color: #003b67;
    margin: 20px;
    border-radius: 15px;
    padding: 20px;
}