li{
    list-style-type: none;
}
a{
    text-decoration: none;
}
.img-contact{
    width: 50px;
    border: 3px solid #ffbe00;;
    border-radius: 34%;
}
.contact-prev{
    background-color: #003b67;
    border-radius: 26px;
    display: flex;
    justify-content: space-between;
    align-items: center;
    margin: 10px;
    cursor: pointer;
    color: white;
}
.contact-prev:hover{
    background-color: #1c5785;
}
.contact-prev span{
    margin-left: 25px;
}

.statistic-container{
    background-color: #003b67;
    margin: 30px;
    border-radius: 30px;
}
.statistic-container h4{
    display: flex;
    justify-content: center;
    color: white;
    font-size: 20px;
    text-decoration: underline;
}
.search{
    height: 50px;
    width: 300px;
    padding: 0 10px;
    border: 0;
    border-radius: 13px;
    color: #003b67;
    outline: 0;
    font-weight: bold;
    font-size: 20px;
    -webkit-transition: all .3s;
    transition: all .3s;
    margin-top: 9px;
}
.search:focus{
    width: 350px;
}
.btn{
    position: fixed;
    bottom: 20px;
    left: 220px;
    border: 0;
    padding: 10px;
    background-color: white;
    cursor: pointer;
    color: #003b67;
    font-weight: bold;
    -webkit-transition: all .3s;
    transition: all .3s;
    border-radius: 10px;
}
.btn:hover{
    background-color: #efefef;
}

.signup-input{
    height: 50px;
    border: 0;
    border-radius: 10px;
    padding-left: 10px;
    margin-top: 15px;
    font-weight: bold;
    outline: 0;
}
.signup-btn{
    border: 0;
    padding: 10px;
    background-color: white;
    cursor: pointer;
    color: #003b67;
    font-weight: bold;
    -webkit-transition: all .3s;
    transition: all .3s;
    margin: 10px;
    border-radius: 10px;
}
.signup-btn:hover{
    background-color: #efefef;
}
.details-container{
    display: flex;
    flex-direction: column;
    align-items: center;
    background-color: #003b67;
    color: white;
    margin: 20px;
    border-radius: 15px;
    padding: 20px;
}

.edit-contact{
    background-color: #003b67;
    margin: 20px;
    border-radius: 15px;
    padding: 20px;
}
body{
    background-color: #ffbe00;
    display: flex;
    align-items: center;
    flex-direction: column;
}
*{
    margin: 0;
    padding: 0;
    font-family: sans-serif;
}

.navbar-container{
    display: flex;
    align-items: center;
    justify-content: center;
    background-color: #003b67;
    color: white;
    height: 50px;
    width: 100%;
    margin: 0;
    padding: 0;
}
.navbar-container li{
    color: white;
    margin-right: 35px;
    -webkit-transition: all 1s;
    transition: all 1s;
    font-weight: bold;
}
.navbar-container li:hover{
    color: #ffbe00;

}
.flex{
    display: flex;
}
.center{
    justify-content: center;
    align-items: center;
}

.full{
    width: 100%;
}
.column{
    flex-direction: column;
}
.logo{
    max-width: 40px;
    margin-top: 5px;
    margin-left: 10px;
    -webkit-transform: rotate(0deg);
            transform: rotate(0deg);
    -webkit-transition: all .3s;
    transition: all .3s;
}
.logo:active{
    -webkit-transform: rotate(360deg);
            transform: rotate(360deg);
}
.home-container{
    background-color: #003b67;
    color: white;
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
    padding: 30px 50px;
    margin-top: 50px;
    max-width: 300px;
    border-radius: 10px;
}
