body{
    background-color: #ffbe00;
    display: flex;
    align-items: center;
    flex-direction: column;
}
*{
    margin: 0;
    padding: 0;
    font-family: sans-serif;
}

.navbar-container{
    display: flex;
    align-items: center;
    justify-content: center;
    background-color: #003b67;
    color: white;
    height: 50px;
    width: 100%;
    margin: 0;
    padding: 0;
}
.navbar-container li{
    color: white;
    margin-right: 35px;
    transition: all 1s;
    font-weight: bold;
}
.navbar-container li:hover{
    color: #ffbe00;

}
.flex{
    display: flex;
}
.center{
    justify-content: center;
    align-items: center;
}

.full{
    width: 100%;
}
.column{
    flex-direction: column;
}
.logo{
    max-width: 40px;
    margin-top: 5px;
    margin-left: 10px;
    transform: rotate(0deg);
    transition: all .3s;
}
.logo:active{
    transform: rotate(360deg);
}
.home-container{
    background-color: #003b67;
    color: white;
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
    padding: 30px 50px;
    margin-top: 50px;
    max-width: 300px;
    border-radius: 10px;
}